.text-center {
  text-align: center;
}

.calendar-container {
  position: relative;
  width: 100%;
  padding-top: 60%;
  /* 16:9 Aspect Ratio */
  max-width: 800px;
  /* Maximum width for larger screens */
  min-width: 500px;
  max-height: 600px;
  /* Maximum height */
  min-height: 300px;
  margin: auto;
  /* Center the calendar */
  overflow: hidden;

}

.calendar-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}